.container {
  position: absolute;
  width: 100%;
  transform: translate(-50%, -50%);
  top: 40%;
  left: 50%;
  

}

.glitch, .glow {
  color: #dfbfbf;
  display: flex;
  justify-content: center;
  left: 50%;
  top: 0;
  font-size: 9vw;
  animation: glitch 5s 5s infinite;
}

.glitch::before, .glow::before {

  content: attr(data-text);
  position: absolute;
  /* left: 0; */
  text-shadow: -5px 0 #cd3091;
  background: black;
  overflow: hidden;
  /* top: 1000px; */
  animation: noise-1 3s linear infinite alternate-reverse, glitch 5s 5.05s infinite;
}

.glitch::after, .glow::after {
  content: attr(data-text);
  position: absolute;
  translate: -2px;
  text-shadow: -5px 0 #27ac32;
  background: black;
  overflow: hidden;
  top: 0;
  animation: noise-2 5s linear infinite alternate-reverse, glitch 5s 5s infinite;
}

/* media query for smartphone */
@media (max-width:480px)  {
  .glitch, .glow {
    font-size: 3rem !important;
    
  }
  .glitch::before, .glow::before {
    translate: -1px;
    animation: noise-1 5s linear infinite alternate-reverse, glitch 5s 5.05s infinite;

  }
  .glitch::after, .glow::after {
    animation: noise-2 7s linear infinite alternate-reverse, glitch 5s 5s infinite;

  }



 
  .subtitle {
    text-align: center;
    font-size: 0.4rem !important;
  }

}


