    .ag-courses_box {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  flex-direction: column;
  -webkit-box-align: start;
  -ms-flex-align: start;
  -ms-flex-wrap: wrap;
  width: 100%;
  padding: 50px 0;
}
.ag-courses_item {
  position: relative;
  margin: 0 100px 70px;
  overflow: hidden;
  transition: 0.3s;  
}
/* media query for small device */
@media (max-width:480px)  {
  .ag-courses_item {
    margin: 0 10px 70px;
  }
}
/* skew ag-courses_item on hover and animate*/
.ag-courses_item:hover {

  transform: skewX(-15deg);
  box-shadow:
  0 0 10px var(--pink),
  0 0 20px var(--pink),
  0 0 40px var(--pink),
  0 0 80px var(--pink),
  0 0 120px var(--pink);

}
.ag-courses_item::before {
  content: '';
  display: none;
  position: absolute;
  top: 0;
  left: -100px;
  width: 0;
  height: 100%;
  background: var(--pink);
  /* transform: skewX(-35deg); */
  z-index: 1;
  transition: 0.5s;

}
.ag-courses_item:hover::before {
  width: 100%;

}
 
.ag-courses-item_link {
  display: block;
  padding: 30px 20px;
  background-color: #121212;

  overflow: hidden;

  position: relative;
}

.ag-courses-item:hover .ag-courses_item {
  text-decoration: none;
  color: #FFF;
}
.ag-courses-item:hover .ag-courses-item_title {
  transform: skewX(-30deg);
}
