/* select even .ag-courses_item::before  */
.ag-courses_item:nth-child(2n)::before {
  background: var(--green);
}
.ag-courses_item:nth-child(2n)::before:hover {
  background: var(--pink);
}
.ag-courses-item:nth-child(2n) .ag-courses-item_bg{
  background-color: var(--green) ;
}

.ag-courses_item:nth-child(2n)::before:hover .ag-courses-item {
  text-decoration: none;
  color: #FFF;
}
.ag-courses_item:nth-child(2n):hover  {
  box-shadow:
  0 0 10px var(--green),
